* {
	box-sizing: border-box;
}

html, body {
	width: 100%;
	height: 100%;
}

body {
	font-family: 'Montserrat';
	font-weight: 400;
	font-size: 14px;
	color: $gray;
	line-height: 1.42;
	-webkit-font-smoothing: antialiased;
}

.container {
	//width: $container-width;
	margin: 0 auto;
}

.wrapper {
	overflow: hidden;
	min-height: 100%;
	//min-width: $container-width + 20px;

	&:after {
		content: "";
		display: block;
		height: $footer-height;
	}
}

.footer {
	height: $footer-height;
	margin-top: -$footer-height;
	//min-width: $container-width + 20px;
}

ul {
	padding: 0;
	margin: 0;
}

ul li {
	list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	font-weight: normal;
}

%clearfix {
	&:after {
		content: "";
		display: block;
		clear: both;
	}
}